import React from "react";
import classes from "./DenOverview.module.css";
import { useTranslation } from 'react-i18next';
import ItemDrops from "../EventViewer/Event/ItemDrops";
import Icon from "../EventViewer/Icon";
import AbilityNames from "../EventViewer/Event/AbilityNames";
import {maxRank} from "../../Util/NestUtil"

const MonDetailLight = props => {
    // eslint-disable-next-line
    const [t, i18n] = useTranslation(["misc"]);
    
    return (
        <div className={classes.Entry}>
            <div className={classes.Center}><Icon details={props.data} /></div>
            <p><strong>Rarity: </strong></p>
            <ul>
                {props.data.Probabilities.map((i, idx) => {
                    return i > 0 ? <li key={idx}>{"☆".repeat(idx+1)}: {i}%</li> : null
                })}
            </ul>
            <p><strong>{t("FLAWLESSIVS")}:</strong> {props.data.FlawlessIVs}</p>
            <p><strong>{t("ABILITY")}:</strong></p><AbilityNames species={props.data.Species} altForm={props.data.AltForm} ability={props.data.Ability} />
            {<ItemDrops level={maxRank(props.data)} bonus={props.data.BonusTableID} drops={props.data.DropTableID} bonusTables={props.bonus} dropTables={props.drops} />}
        </div>
        );
};

export default MonDetailLight;