import nestContent from "../Assets/Data/NestContent";

const getNest = (game, nest) => {
    return nestContent[game][nest]["Entries"];
}

const minRank = mon => {
    for(var i = 0; i < 5; i++) {
        if(mon.Probabilities[i] > 0) {
            return i;
        }
    }
    return -1;
}

const maxRank = mon => {
    for(var i = 4; i >= 0; i--) {
        if(mon.Probabilities[i] > 0) {
            return i;
        }
    }
    return -1;
}

function compare(a, b) {
    return minRank(a) > minRank(b) ? 1 : minRank(a) < minRank(b) ? -1 : 0;
  }

const shrinkNest = nest => {
    const usedUp = new Set();
    return nest.reduce(((prev, cur) => {
        if (maxRank(cur) === -1) {
            return prev;
        }
        const tmp = cur.Species + "." + minRank(cur) + "." + maxRank(cur) + "." + cur.FlawlessIVs + "." + cur.Ability + "." + cur.AltForm + "." + cur.IsGigantamax + "." + cur.Nature;
        if(usedUp.has(tmp)) {
            return prev;
        } else {
            usedUp.add(tmp);
            return prev.concat(cur);
        }
    }), []).sort(compare);
}

export default shrinkNest;
export { getNest, shrinkNest, minRank, maxRank };